import { BitrateMenu } from "../../utils/classes/BitrateMenu";
import { CustomLogger } from "../../utils/classes/CustomLogger";
export class QualityEnhancer {
    constructor() {
        this.init = async () => {
            this.bitrate_menu.init();
            window.document.onkeydown = async (e) => {
                if (e.key === "G") {
                    console.log("Resetting");
                    await this.reset_video_quality();
                }
            };
        };
        this.reset_video_quality = async () => {
            const available_bitrates = this.bit;
            const best = await this.bitrate_menu.get_available_bitrates()[0];
            await this.bitrate_menu.set_bitrate(best);
        };
        this.logger = new CustomLogger("[QualityEnhancer]");
        this.bitrate_menu = new BitrateMenu();
    }
}
